.ampKnob {
    width: 5vw;
    height: 5vw;
}

.navbar {
    width: 99vw;
    background-color: black;
    background-image: url('/images/brushedgold.png');
    background-size: 100% 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    border: 8px black solid;
    -webkit-box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.5) inset;
            box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.5) inset;
    border-radius: 16px;
    height: 20vh;
    overflow: hidden;
}

.navbar p {
    font-family: 'audiowide', 'Arial Narrow', Arial, sans-serif;
    font-weight: lighter;
    color: black;
    text-decoration: none;
}

.clickable:hover {
    cursor: pointer;
    text-decoration: none;
}


